'use strict';

// –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏

(function () {
  var LoginModal = function () {
    window.Modal.call(this, document.querySelector('.modal--login')); // –Ω–∞—Å–ª–µ–¥—É–µ–º —Å–≤–æ–π—Å—Ç–≤–∞ —Ä–æ–¥–∏—Ç–µ–ª—è
    this.buttonOpen = document.querySelector('.user-nav__link--sigin'); // –∫–Ω–æ–ø–∫–∞ –æ—Ç–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞

    this.LoginForm = new window.LoginForm(document.forms.auth); // –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä —Ñ–æ—Ä–º—ã –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏

    this.start(); // –∑–∞–ø—É—Å–∫ –ª–æ–≥–∏–∫–∏
  };

  // –ù–∞—Å–ª–µ–¥—É–µ–º –º–µ—Ç–æ–¥—ã —Ä–æ–¥–∏—Ç–µ–ª—è
  LoginModal.prototype = Object.create(window.Modal.prototype);
  LoginModal.prototype.constructor = LoginModal;

  // –ó–∞–ø—É—Å–∫ –ª–æ–≥–∏–∫–∏
  LoginModal.prototype.start = function () {
    window.Modal.prototype.start.call(this); // –ª–æ–≥–∏–∫–∞ —Ä–æ–¥–∏—Ç–µ–ª—è
    this.buttonOpen.addEventListener('click', this.onOpenModal.bind(this)); // –æ—Ç–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  };

  LoginModal.prototype.onOpenModal = function (evt) {
    window.Modal.prototype.onOpenModal.call(this, evt);

    this.focusInput();
  };

  // –§–æ–∫—É—Å–∏—Ä–æ–≤–∫–∞ –Ω–∞ –Ω—É–∂–Ω–æ–º –∏–º–ø—É—Ç–µ
  LoginModal.prototype.focusInput = function () {
    try {
      this.LoginForm.$login.value = localStorage.getItem('login');
    } catch (e) {
      console.warn('–ù–µ —É–¥–∞–ª–æ—Å—å –ø–æ–ª—É—á–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ login –≤ localStorage');
    }

    this.LoginForm.$login.value ? this.LoginForm.$password.focus() : this.LoginForm.$login.focus();
  };

  window.LoginModal = LoginModal;
})();
