'use strict';

(function () {
  var HaircutForm = function (elem) {
    window.Form.call(this, elem);
    this.$name = this.$inputs.name;
    this.$phone = this.$inputs.phone;
  };

  HaircutForm.prototype = Object.create(window.Form.prototype);
  HaircutForm.prototype.constructor = HaircutForm;

  // –°–æ–±—ã—Ç–∏–µ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
  HaircutForm.prototype.onSubmitForm = function (evt) {
    window.Form.prototype.onSubmitForm.call(this); // –ª–æ–≥–∏–∫–∞ —Ä–æ–¥–∏—Ç–µ–ª—è

    evt.preventDefault();
    var rand = Math.floor(Math.random() * 2);
    rand ? new window.NoticeModal(true) : new window.NoticeModal();
  };

  // –°–æ–±—ã—Ç–∏–µ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —Ñ–æ—Ä–º—ã
  HaircutForm.prototype.onValidateForm = function () {
    this.validateName();
    this.validatePhone();
  };

  HaircutForm.prototype.validateName = function () {
    this.—Åharacters = this.$name.value.length; // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–∏–º–≤–æ–ª–æ–≤ –≤–≤–µ–¥–µ–Ω–Ω–æ–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º

    if (!this.—Åharacters) {
      this.message = '–í–≤–µ–¥–∏—Ç–µ –≤–∞—à–µ –∏–º—è';
    } else if (this.—Åharacters < 2) {
      this.message = '–ò–º—è –¥–æ–ª–∂–µ–Ω–æ –±—ã—Ç—å –±–æ–ª–µ–µ 1-—Ö —Å–∏–º–≤–æ–ª–∞';
    } else if (this.—Åharacters > 12) {
      this.message = '–ò–º—è –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –º–µ–Ω–µ–µ 13-—Ç–∏ —Å–∏–º–≤–æ–ª–æ–≤';
    } else {
      this.message = '';
    }

    this.alert(this.$name); // —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
  };

  HaircutForm.prototype.validatePhone = function () {
    this.—Åharacters = this.$phone.value.length; // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–∏–º–≤–æ–ª–æ–≤ –≤–≤–µ–¥–µ–Ω–Ω–æ–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º

    if (!this.—Åharacters) {
      this.message = '–í–≤–µ–¥–∏—Ç–µ –≤–∞—à–µ —Ç–µ–ª–µ—Ñ–æ–Ω';
    } else if (this.—Åharacters < 2) {
      this.message = '–¢–µ–ª–µ—Ñ–æ–Ω –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –±–æ–ª–µ–µ 1-–≥–æ —Å–∏–º–≤–æ–ª–∞';
    } else if (this.—Åharacters > 8) {
      this.message = '–¢–µ–ª–µ—Ñ–æ–Ω –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –º–µ–Ω–µ–µ 9-—Ç–∏ —Å–∏–º–≤–æ–ª–æ–≤';
    } else {
      this.message = '';
    }

    this.alert(this.$phone); // —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
  };

  window.HaircutForm = HaircutForm;
})();
