'use strict';

// –§–æ—Ä–º–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏

(function () {
  var LoginForm = function (elem) {
    window.Form.call(this, elem); // –Ω–∞—Å–ª–µ–¥—É–µ–º —Ä–æ–¥–∏—Ç–µ–ª—å—Å–∫–∏–µ —Å–≤–æ–π—Å—Ç–≤–∞
    this.$modal = this.$form.parentElement; // –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –≤ –∫–æ—Ç–æ—Ä–æ–º –Ω–∞—Ö–æ–¥–∏—Ç—Å—è —Ñ–æ—Ä–º–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
    this.$login = this.$inputs.login; // –ø–æ–ª–µ –≤–≤–æ–¥–∞ –ª–æ–≥–∏–Ω–∞
    this.$password = this.$inputs.password; // –ø–æ–ª–µ –≤–≤–æ–¥–∞ –ø–∞—Ä–æ–ª—è
  };

  // –ù–∞—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ –º–µ—Ç–æ–¥–æ–≤ —Ä–æ–¥–∏—Ç–µ–ª—è
  LoginForm.prototype = Object.create(window.Form.prototype);
  LoginForm.prototype.constructor = LoginForm;

  // –°–æ–±—ã—Ç–∏–µ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
  LoginForm.prototype.onSubmitForm = function (evt) {
    window.Form.prototype.onSubmitForm.call(this); // –ª–æ–≥–∏–∫–∞ —Ä–æ–¥–∏—Ç–µ–ª—è

    localStorage.setItem('login', this.$login.value);
  };

  // –°–æ–±—ã—Ç–∏–µ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —Ñ–æ—Ä–º—ã
  LoginForm.prototype.onValidateForm = function () {
    window.Form.prototype.onValidateForm.call(this); // –ª–æ–≥–∏–∫–∞ —Ä–æ–¥–∏—Ç–µ–ª—è
    this.validateLogin(); // –≤–∞–ª–∏–¥–∞—Ü–∏—è –ø–æ–ª—è –ª–æ–≥–∏–Ω
    this.validatePassword(); // –≤–∞–ª–∏–¥–∞—Ü–∏—è –ø–æ–ª—è –ø–∞—Ä–æ–ª—å
  };

  // –í–∞–ª–∏–¥–∞—Ü–∏—è –ø–æ–ª—è –ª–æ–≥–∏–Ω
  LoginForm.prototype.validateLogin = function () {
    this.—Åharacters = this.$login.value.length; // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–∏–º–≤–æ–ª–æ–≤ –≤–≤–µ–¥–µ–Ω–Ω–æ–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º

    if (!this.—Åharacters) {
      this.message = '–í–≤–µ–¥–∏—Ç–µ –ª–æ–≥–∏–Ω';
    } else if (this.—Åharacters < 3) {
      this.message = '–õ–æ–≥–∏–Ω –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –±–æ–ª–µ–µ 2-—Ö —Å–∏–º–≤–æ–ª–æ–≤';
    } else if (this.—Åharacters > 12) {
      this.message = '–õ–æ–≥–∏–Ω –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –º–µ–Ω–µ–µ 13-—Ç–∏ —Å–∏–º–≤–æ–ª–æ–≤';
    } else {
      this.message = '';
    }

    this.alert(this.$login); // —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
  };

  // –í–∞–ª–∏–¥–∞—Ü–∏—è –ø–æ–ª—è –ø–∞—Ä–æ–ª—å
  LoginForm.prototype.validatePassword = function () {
    this.—Åharacters = this.$password.value.length; // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–∏–º–≤–æ–ª–æ–≤ –≤–≤–µ–¥–µ–Ω–Ω–æ–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º

    if (!this.—Åharacters) {
      this.message = '–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å';
    } else if (this.—Åharacters < 6) {
      this.message = '–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –±–æ–ª–µ–µ 5-—Ç–∏ —Å–∏–º–≤–æ–ª–æ–≤';
    } else if (this.—Åharacters > 18) {
      this.message = '–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –º–µ–Ω–µ–µ 19-—Ç–∏ —Å–∏–º–≤–æ–ª–æ–≤';
    } else {
      this.message = '';
    }

    this.alert(this.$password); // —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
  };



  window.LoginForm = LoginForm;
})();
