'use strict';

// –†–æ–¥–∏—Ç–µ–ª—å—Å–∫–∏–π –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä –¥–ª—è –º–æ–¥–∞–ª—å–Ω—ã—Ö –æ–∫–æ–Ω

(function () {
  var Modal = function (elem) {
    this.$modal = elem; // —ç–ª–µ–º–µ–Ω—Ç –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    this.overlay = document.querySelector('.overlay'); // —ç–ª–µ–º–µ–Ω—Ç –ø–æ–¥–ª–æ–∂–∫–∏ –∑–∞—Ç–µ–º–Ω–µ–Ω–∏—è
    this.buttonOpen = null; // –∫–Ω–æ–ø–∫–∞ –æ—Ç–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    this.buttonClose = this.$modal.querySelector('.modal__close'); // –∫–Ω–æ–ø–∫–∞ –∑–∞–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  };

  // –ó–∞–ø—É—Å–∫ –ª–æ–≥–∏–∫–∏
  Modal.prototype.start = function () {
    this.buttonClose.addEventListener('click', this.onCloseModal.bind(this));
  };

  // –û—Ç–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  Modal.prototype.open = function () {
    this.$modal.classList.remove('hidden');
    this.overlay.classList.remove('hidden');
  };

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  Modal.prototype.close = function () {
    this.$modal.classList.add('hidden');
    this.overlay.classList.add('hidden');
  };

  // –°–æ–±—ã—Ç–∏–µ –∑–∞–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  Modal.prototype.onCloseModal = function () {
    this.close();

    document.removeEventListener('keydown', this.onKeyPress.bind(this));
  };

  // –°–æ–±—ã—Ç–∏–µ –æ—Ç–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  Modal.prototype.onOpenModal = function (evt) {
    evt.preventDefault();

    this.open();
    document.addEventListener('keydown', this.onKeyPress.bind(this));
  };

  Modal.prototype.onKeyPress = function (evt) {
    var ESC = 27;

    if (evt.keyCode === ESC) {
      this.onCloseModal();
    }
  };

  window.Modal = Modal;
})();
