'use strict';

// –†–æ–¥–∏—Ç–µ–ª—å—Å–∫–∏–π –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä –¥–ª—è —Ñ–æ—Ä–º

(function () {
  var Form = function (elem) {
    this.$form = elem; // —ç–ª–µ–º–µ–Ω—Ç —Ñ–æ—Ä–º—ã

    this.$inputs = this.$form.elements; // –ø–æ–ª—è —Ñ–æ—Ä–º—ã
    this.$submit = this.$form.querySelector('button[type="submit"]'); // –∫–Ω–æ–ø–∫–∞ –æ—Ç–ø—Ä–∞–≤–∫–∏

    this.message = ''; // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
    this.—Åharacters = 0; // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–∏–º–≤–æ–ª–æ–≤ –≤–≤–µ–¥–µ–Ω–Ω–æ–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º

    this.start();
  };

  Form.prototype.start = function () {
    this.$submit.addEventListener('click', this.onEditForm.bind(this)); // –Ω–∞–∂–∞—Ç–∏–µ –Ω–∞ –∫–Ω–æ–ø–∫—É –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
    this.$form.addEventListener('submit', this.onSubmitForm.bind(this)); // –æ—Ç–ø—Ä–∞–≤–∫–∞ —Ñ–æ—Ä–º—ã
  };

  // –°–æ–±—ã—Ç–∏–µ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –≤–≤–æ–¥–∞ –≤ –ø–æ–ª–µ
  Form.prototype.onEditForm = function () {
    this.onValidateForm();

    this.$form.addEventListener('input', this.onValidateForm.bind(this), true);
  };

  // –°–æ–±—ã—Ç–∏–µ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
  Form.prototype.onSubmitForm = function () {
    this.$form.removeEventListener('input', this.onEditForm.bind(this), true);
  };

  // –°–æ–±—ã—Ç–∏–µ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —Ñ–æ—Ä–º—ã
  Form.prototype.onValidateForm = function () {
    // –ö–æ–¥..
  };

  // –£–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
  Form.prototype.alert = function (input) {
    input.setCustomValidity(this.message);
    this.effect(input);
  };

  // –≠—Ñ—Ñ–µ–∫—Ç—ã –ø—Ä–∏ –æ—à–∏–±–∫–∏
  Form.prototype.effect = function (input) {
    this.message ? input.classList.add('error') : input.classList.remove('error');
  };

  window.Form = Form;
})();
